'use client'

import { client } from '@/lib/sanity'
import { issuesListQuery } from '@/lib/queries'
import Link from 'next/link'
import { useEffect, useState } from 'react'

export default function HomePage() {
  const [issues, setIssues] = useState<any[]>([])
  const [menuOpen, setMenuOpen] = useState(false)

  useEffect(() => {
    client.fetch(issuesListQuery).then((data) => {
      setIssues(data)
    })
  }, [])

  return (
    <div className="min-h-screen bg-white flex">
      {/* 왼쪽: 메인 콘텐츠 */}
      <main className="flex-1 p-12 lg:p-20">
        {/* 로고 */}
        <div className="mb-16">
          <h1 className="text-4xl font-bold mb-2">글짜씨</h1>
          <div className="w-8 h-1 bg-black"></div>
        </div>

        {/* 소개 텍스트 */}
        <div className="max-w-2xl space-y-8 text-lg leading-relaxed">
          <p>
            올해 '일기를 시작한 제8대 한국타이포그라피학회는 디지털 환경에서의 타이포그래피를 돌려본 한
            상과 실천 동을 살펴보며 디지털 타이포그라피의 경계성을 탐구한다. 이에 대한 연장선으로 제8대
            한국타이포그라피학회는 '글짜씨'를 통해 디지털 환경 속의 타이포그래피를 살펴하고 이를 행을 통
            해 공유하는 프로젝트를 진행한다.
          </p>

          <p>
            『글짜씨』는 2009년에 창간해 지난 15년간 학회 구성원과 국내외 타이포그래피 연구자, 디자이너
            의 연구 성과를 기록한 학회 학술지다. 학술지는 연구 분야의 연구 및 활동을 공유하는 창으로 기능
            한다. 미러한 학회와 학술지의 가치는 함의 기본정신인 '공짜'와 '글유와도 맞닿아 있다는 점에서도
            『글짜씨』 웹 버전의 의미는 찾을 수 있다.
          </p>

          <p>
            '글짜씨'는 웹 브라우저 내에서의 읽기 환경을 고려해 『글짜씨』를 발해 출판한 실험 사이트이다.
            현존하는 디지털 읽기 환경의 아쉬운 점을 보완해 디지털 환경에서의 읽글 읽기 경험을 실험하고자
            했으며, 『글짜씨』의 일부 내용을 포함하지 않고 있다.
          </p>
        </div>

        {/* 호 목록 (모바일에서만 표시) */}
        <div className="mt-16 lg:hidden">
          <h2 className="text-2xl font-bold mb-6">호</h2>
          <div className="space-y-2">
            {issues.map((issue) => (
              <Link
                key={issue._id}
                href={`/issues/${issue.number}`}
                className="block py-3 border-b hover:bg-gray-50"
              >
                <div className="flex items-center justify-between">
                  <span className="font-medium">{issue.number}</span>
                  <span className="text-sm text-gray-600">{issue.title}</span>
                </div>
              </Link>
            ))}
          </div>
        </div>
      </main>

      {/* 오른쪽: 사이드바 (데스크톱) */}
      <aside className="hidden lg:block w-80 border-l p-12 sticky top-0 h-screen overflow-auto">
        <div className="mb-8">
          <h2 className="text-sm font-bold mb-2">글짜씨</h2>
          <div className="w-6 h-0.5 bg-black"></div>
        </div>

        {/* 메뉴 */}
        <nav className="space-y-6 text-sm">
          <div>
            <p className="text-gray-600 mb-2">
              올해 '일기를 시작한 제8대 한국타이포그라피학회는 디지털 환경에서의 타이포그래피를
              돌려본 한상과 실천 동을 살펴보며 디지털 타이포그라피의 경계성을 탐구한다.
            </p>
          </div>

          <div>
            <p className="text-gray-600 mb-4">
              『글짜씨』는 2009년에 창간해 지난 15년간 학회 구성원과 국내외 타이포그래피 연구자,
              디자이너의 연구 성과를 기록한 학회 학술지다.
            </p>
          </div>

          <div>
            <p className="text-gray-600 mb-6">
              '글짜씨'는 웹 브라우저 내에서의 읽기 환경을 고려해 『글짜씨』를 발해 출판한 실험
              사이트이다.
            </p>
          </div>

          {/* 호 목록 */}
          <div className="pt-6 border-t">
            {issues.map((issue) => (
              <Link
                key={issue._id}
                href={`/issues/${issue.number}`}
                className="block py-3 hover:text-blue-600 transition"
              >
                <div className="text-sm">
                  <div className="font-bold mb-1">{issue.number}</div>
                  <div className="text-gray-600">{issue.title}</div>
                </div>
              </Link>
            ))}
          </div>
        </nav>
      </aside>

      {/* 모바일 햄버거 메뉴 */}
      <button
        className="lg:hidden fixed top-6 right-6 z-50 w-10 h-10 flex items-center justify-center"
        onClick={() => setMenuOpen(!menuOpen)}
      >
        <div className="w-6 space-y-1.5">
          <div className="h-0.5 bg-black"></div>
          <div className="h-0.5 bg-black"></div>
          <div className="h-0.5 bg-black"></div>
        </div>
      </button>

      {/* 모바일 메뉴 */}
      {menuOpen && (
        <div className="lg:hidden fixed inset-0 bg-white z-40 p-12 overflow-auto">
          <button
            className="absolute top-6 right-6 text-2xl"
            onClick={() => setMenuOpen(false)}
          >
            ×
          </button>

          <div className="mt-16">
            <h2 className="text-2xl font-bold mb-8">글짜씨</h2>

            <div className="space-y-6 text-sm">
              <p className="text-gray-600">
                올해 '일기를 시작한 제8대 한국타이포그라피학회는 디지털 환경에서의
                타이포그래피를 돌려본 한상과 실천 동을 살펴보며 디지털 타이포그라피의
                경계성을 탐구한다.
              </p>

              <div className="pt-6 border-t">
                {issues.map((issue) => (
                  <Link
                    key={issue._id}
                    href={`/issues/${issue.number}`}
                    className="block py-3 border-b"
                    onClick={() => setMenuOpen(false)}
                  >
                    <div className="font-bold mb-1">{issue.number}</div>
                    <div className="text-gray-600 text-xs">{issue.title}</div>
                  </Link>
                ))}
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}