// 특정 호의 모든 데이터 가져오기
export const issueQuery = `
  *[_type == "issue" && number == $number][0]{
    _id,
    number,
    title,
    publishDate,
    coverImage,
    "sections": *[_type == "section"] | order(order asc) {
      _id,
      title,
      slug,
      order,
      "articles": *[_type == "article" && references(^._id) && issue._ref == $issueId] | order(order asc) {
        _id,
        title,
        slug,
        author,
        order
      }
    }
  }
`

// 아티클 상세 가져오기
export const articleQuery = `
  *[_type == "article" && slug.current == $slug][0]{
    _id,
    title,
    slug,
    author,
    authorBio,
    content,
    references,
    imageSources,
    "issue": issue->{
      number,
      title
    },
    "section": section->{
      title,
      slug
    }
  }
`

// 호 목록 가져오기
export const issuesListQuery = `
  *[_type == "issue"] | order(number desc) {
    _id,
    number,
    title,
    publishDate,
    coverImage
  }
`

// 특정 호의 섹션과 아티클 가져오기 (수정됨)
export const issueSectionsQuery = `
  *[_type == "issue" && number == $number][0]{
    _id,
    number,
    title,
    publishDate,
    "sections": *[_type == "section"] | order(order asc) {
      _id,
      title,
      slug,
      order,
      "articles": *[_type == "article" && references(^._id) && issue->number == $number] | order(order asc) {
        _id,
        title,
        slug,
        author,
        authorBio,
        order
      }
    }
  }
`